@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
.header{display:flex;justify-content:center;align-items:center;width:100%;height:10vh;font-weight:700;font-size:x-large;transition:0.5s;background-color:black;color:white;outline:5px solid transparent}.header h1{font-size:xx-large;border-left:5px solid white;border-bottom:5px solid transparent;padding-left:5px;padding-top:5px;transition:0.5s;cursor:pointer}.header h1:hover{border-bottom:5px solid white;border-left:5px solid transparent}

@media screen and (max-width: 850px){.card{margin-top:10px !important;height:50% !important;width:98% !important}}.card{width:30%;height:80%;display:flex;flex-direction:column;justify-content:space-between;align-items:center;transition:0.5s;margin-top:10px;background-color:#3d3d3d;color:white;cursor:pointer}.card .card-headline{text-align:center;font-weight:500}.card .card-types{width:100%;display:flex;align-items:center;justify-content:center}.card .card-types p{margin:0px !important;text-align:center;width:100%;padding:10px 15px;background-color:black;color:white;font-weight:500}.card .card-types .poison{background-color:#02fd02}.card .card-types .grass{background-color:green}.card .card-types .fire{background-color:orange}.card .card-types .flying{background-color:#010155}.card .card-types .water{background-color:blue}.card .card-types .bug{background-color:#51e33a;color:black}.card .card-types .fairy{background-color:pink;color:white}.card .card-types .electric{background-color:yellow;color:black}.card .card-types .ground{background-color:#1c0101;color:white}.card .card-types .fighting{background-color:red;color:white}.card .card-types .psychic{background-color:purple;color:white}.card .card-types .rock{background-color:grey;color:white}.card .card-types .steel{background-color:darkgrey;color:black}.card .card-types .ice{background-color:lightblue;color:black}.card .card-types .ghost{background-color:whitesmoke;color:black}.card img{width:90%;height:70%;background-color:transparent}.card button{margin:0 auto;font-size:large;background-color:black;color:white;padding:10px 15px;border-radius:10px;cursor:pointer}.card button:hover{background-color:#010155;color:white}

@media screen and (max-width: 850px){.list{width:50% !important;padding-bottom:25px}}.list{width:70%;height:100%;overflow-x:hidden;overflow-y:auto;display:flex;justify-content:space-evenly;flex-direction:row;flex-wrap:wrap;background-color:white}.list .list-types{overflow-x:auto;padding-left:10px;padding-right:10px;width:100%;height:10vh;background-color:#3D3D3D;color:white;display:flex;justify-content:flex-start;align-items:center}.list .list-types p{margin-left:5px;margin-right:5px;padding:10px 15px;background-color:whitesmoke;border-radius:5px;color:black;cursor:pointer}.list button{width:80%;margin-top:10px;margin-bottom:10px;height:10vh;background-color:#3D3D3D;border-radius:5px;color:white;font-size:x-large;cursor:pointer}

@media screen and (max-width: 850px){.selected-area{width:50% !important}}.selected-area{width:30%;height:100%;overflow-x:hidden;overflow-y:auto;display:flex;flex-direction:row;flex-wrap:wrap}.selected-area .selected-card{width:100%;display:flex;flex-direction:column}.selected-area .selected-card .selected-card-headline{background-color:#3d3d3d;text-align:center;color:white;padding:15px;margin:0px}.selected-area .selected-card img{margin:5px auto;width:55%}.selected-area .selected-card .selected-card-types{display:flex;justify-content:center}.selected-area .selected-card .selected-card-types p{margin:10px 15px;background-color:#262626;color:white;padding:10px 15px;border-radius:10px}.selected-area .selected-card table thead{border:5px solid black !important;font-size:large;font-weight:700 !important}.selected-area .selected-card table thead th{border:1px solid rgba(0,0,0,0.15);margin:0px}.selected-area .selected-card table tbody td{text-align:center;border-bottom:1px solid rgba(0,0,0,0.15)}

@media screen and (max-width: 850px){.main-area .link{width:50% !important}.main-area .selected-area{width:50% !important}}.main-area{width:100%;height:90vh;display:flex}

html,body{font-family:"Ubuntu",sans-serif}html,body{padding:0px;margin:0px}html a,body a{color:inherit;text-decoration:none}html a:visited,body a:visited{color:inherit;text-decoration:none}html a:active,body a:active{color:inherit;text-decoration:none}html a:link,body a:link{color:inherit;text-decoration:none}html button,body button{border:none}

